import React, { useState } from "react";
import LogoFacebook from "../../../../assets/icon-facebook.svg";
import LogoApple from "../../../../assets/icon-apple.svg";
import { login } from "../../../../store/actions/auth";
import { connect } from "react-redux";
import { useHistory } from "react-router-dom";
import Button from "../../../UI/ButtonForm/ButtonForm";
import { GAHandler } from "../../../../utils/GA";
import { errorStringHandler } from "../../../../utils/ErrorStrings.js";

const defaultOptions = {
  loop: true,
  autoplay: true,
};

const SignIn = ({ styles, inputType, setinputType, login, isLoging, setTypeContent, loginError }) => {
  const [userName, setUserName] = useState("");
  const [password, setPassword] = useState("");

  const history = useHistory();
  const loginUser = (e) => {
    e.preventDefault();
    login(userName, password)
      .then(() => {
        GAHandler("Iniciar Sesin", "IniciarSesin_Exitosa");
      })
      .catch(() => {
        GAHandler("Iniciar Sesin", "IniciarSesin_Erronea");
      });
  };
  return (
    <form className={styles.form} onSubmit={loginUser}>
      <div className={styles.modalAlert__inputContainer}>
        <input type='text' placeholder='Nombre de Usuario' name='user' id='user' className={styles.modalAlert__input} onChange={(e) => setUserName(e.target.value)} />
      </div>

      <div className={`${styles.modalAlert__inputContainer} ${styles.modalAlert__inputContainer__password}`}>
        <input type={inputType} placeholder='Ingres tu Contrasea' name='password' id='password' className={styles.modalAlert__input} onChange={(e) => setPassword(e.target.value)} />
        <figure className={`${styles.modalAlert__inputEye} ${inputType === "text" ? styles.hide : ""}`} onClick={() => setinputType(inputType === "password" ? "text" : "password")} />
      </div>
      {loginError ? (
        <p className={styles.error} style={{ marginTop: "10px" }}>
          {errorStringHandler(loginError.message)}
        </p>
      ) : null}

      <h3
        className={`${styles.modalAlert__subText} ${styles.recover}`}
        onClick={() => {
          GAHandler("Iniciar Sesin", "IniciarSesin_ClickRecuperarContrasea");
          setTypeContent("recover");
        }}
      >
        Recuperar <span>Contrasea</span>
      </h3>

      <Button type={"submit"} isLoading={isLoging} customStyle={styles.sign}>
        Iniciar Sesin
      </Button>
      {/* 
      <h3 className={styles.modalAlert__subText}>
        Tambien pods<span> registrarte con:</span>
      </h3> */}

      {/* <div className={styles.modalAlert__registerContainer}>
        <figure className={styles.modalAlert__register}>
          <img src={LogoFacebook} alt='facebook logo' />
        </figure>
        <figure className={styles.modalAlert__register}>
          <img src={LogoApple} alt='apple logo' />
        </figure>
      </div> */}
    </form>
  );
};

const mapStateToProps = (state) => {
  return {
    isLoging: state.authReducer.isLoging,
    loginError: state.authReducer.loginError,
  };
};

export default connect(mapStateToProps, { login })(SignIn);
