import React from "react";
import Lottie from "react-lottie";
import loadingData from "../../../assets/flixxo-loading-button.json";
import styles from "./ButtonForm.module.scss";
const defaultOptions = {
  loop: true,
  autoplay: true,
};

const ButtonForm = ({ type = "button", disabled, onClick, isLoading, children, customStyle }) => {
  return (
    <button type={type} className={`${styles.buttonForm} ${customStyle || ""}`} disabled={disabled} onClick={onClick}>
      {isLoading ? <Lottie options={{ animationData: loadingData, ...defaultOptions }} width={50} speed={1} /> : children}
    </button>
  );
};

export default ButtonForm;
