import React, { useCallback } from "react";
import styles from "./CastButton.module.scss";
import { ReactComponent as CastIcon } from "../../../assets/cast-button.svg";
import { useCast, useMedia } from "react-chromecast";

const CastButton = ({ weblinkURL }) => {
  const cast = useCast({
    initialize_media_player: "DEFAULT_MEDIA_RECEIVER_APP_ID",
    auto_initialize: true,
  });

  //   const handleClick = useCallback(async () => {
  //     console.log("clicked");
  //     if (cast.castReceiver) {
  //       await cast.handleConnection();
  //       if (cast.isConnect) {
  //         playVideo();
  //       }
  //     } else {
  //       console.log("no cast");
  //     }
  //   }, [cast.castReceiver, cast.handleConnection]);

  const handleClick = async () => {
    console.log("clicked");

    await cast.handleConnection();
    if (cast.isConnect) {
      playVideo();
    }
  };

  const media = useMedia();
  const playVideo = async () => {
    if (media) {
      await media.playMedia(weblinkURL);
    }
  };

  return (
    <button onClick={handleClick} className={styles.castButton}>
      <CastIcon />
    </button>
  );
};

export default CastButton;
