import React from "react";
import styles from "./CarouselItem.module.scss";
import ContentInfo from "../../UI/ContentInfo/ContentInfo";
import { Slide } from "pure-react-carousel";
import { getHero, getSwitchingThumbUrlSerie, getTileCoverUrl, getMainCover } from "../../../utils/thumbsManager";
import "pure-react-carousel/dist/react-carousel.es.css";
import { useMediaQuery } from "react-responsive";
import { ReactComponent as LeftGradient } from "../../../assets/left-gradient.svg";
import { TYPE_SERIE } from "../../../utils/constants";
const CarouselItem = ({ video, index, fromLanding, fromHome, isEpisode }) => {
  const isMobile = useMediaQuery({ query: "(max-width: 600px)" });

  // const getMainThumbUrl = () => {
  //   const mainThumbUrl = video.contentType === TYPE_SERIE || video.isSerieEpisode === true ? getSwitchingThumbUrlSerie(video, "cover", 0, true) : getTileCoverUrl(video);

  //   return mainThumbUrl;
  // };

  const getMainThumbUrl = () => {
    const type = isMobile ? "xcover" : "hero";
    // const mainThumbUrl = video.contentType === TYPE_SERIE ? getMainCover(video, type) : getMainCover(video, type);
    // if (video.contentType === TYPE_SERIE) {
    //   video.media = video.contentmedia;
    // }
    const mainThumbUrl = video.contentType === TYPE_SERIE ? video.media[0].sizes[type] : getMainCover(video, type);
    return mainThumbUrl;
  };
  const mainThumbUrl = getMainThumbUrl();

  return (
    <Slide index={index} style={{ paddingLeft: isMobile ? "0px" : "58px" }} classNameVisible={styles.visible} classNameHidden={styles.hidden}>
      <div className={`${styles.carousel}`}>
        <div className={styles.contentInfo}>
          <ContentInfo video={video} fromLanding={fromLanding} fromHome={fromHome} isEpisode={isEpisode} />
        </div>
        <div className={`${styles.carouselBackground}`}>
          {/* <LeftGradient className={styles.leftGradient} /> */}
          <figure className={styles.carouselLeftGradient} />
          <img src={mainThumbUrl} alt='test' />
          <figure className={styles.carouselBottomGradient} />
        </div>
      </div>
    </Slide>
  );
};

export default CarouselItem;
