import React, { useEffect, useState, useCallback } from "react";
import styles from "./ContentLanding.module.scss";

import axios from "axios";
import { setPlayerDuration, setPlayerEnd, setPlayerProgress } from "../../store/actions/media";
import { getSerieById, get as getContentById } from "../../store/actions/contentUnauth";
import {
  getWebLink,
  removeContentWeblinkURL,
  setCurrentContentForLanding,
  removeCarrouselForLanding,
  setCurrentSerieForLanding,
  setCarrouselForLanding,
  removeCurrentContentForLanding,
  removeCurrentSerieForLanding,
} from "../../store/actions/content";
import { connect } from "react-redux";
import SerieSlider from "../../components/SerieSlider/SerieSlider";
import Loading from "../../components/UI/Loading/Loading";
import HeaderLanding from "../../components/HeaderLanding/HeaderLanding";
import GoBack from "../../components/UI/GoBack/GoBack";
import { getHero } from "../../utils/thumbsManager";
import PlayerLanding from "../../components/PlayerLanding/PlayerLanding";
import Slider from "../../components/Slider/Slider";

const ContentLanding = ({
  getWebLink,
  currentContentLanding,
  currentCarruselLanding,
  removeCarrouselForLanding,
  setCarrouselForLanding,
  currentSerieLanding,
  contentPurchased,
  weblinkURL,
  removeContentWeblinkURL,
  getSerieById,
  getContentById,
  setCurrentContentForLanding,
  setCurrentSerieForLanding,
  removeCurrentContentForLanding,
  removeCurrentSerieForLanding,
  setPlayerDuration,
  setPlayerEnd,
  setPlayerProgress,
  history,
  ...props
}) => {
  const [currentSeason, setCurrentSeason] = useState(0);
  const [subtitles, setSubtitles] = useState([]);

  // const mainThumbUrl = currentContentLanding.contentType === 2 ? getSwitchingThumbUrlSerie(currentContentLanding, "cover", 0, true) : getTileCoverUrl(currentContentLanding);
  const mainThumbUrl = getHero(currentContentLanding);

  const isEpisode = props.location.pathname.includes("series");

  const findInPurchased = (contentToFind) => {
    return contentPurchased.find((cont) => {
      return cont.uuid === contentToFind.uuid;
    });
  };

  useEffect(() => {
    window.scrollTo({
      top: 0,
    });
    const uuid = props.match.params.id;
    const path = props.location.pathname;

    const fetch = async () => {
      try {
        if (path.includes("series")) {
          const response = await getSerieById(uuid);

          //setCurrentContentForLanding({ ...response.value.season[0].content[0], category: { ...response.value.category }, author: { ...response.value.author }, episodeNumber: "01" });

          setCurrentContentForLanding({
            ...response.value,
            category: { ...response.value.category },
            author: { ...response.value.author },
            noEpisodeSelected: true,
            releaseDate: response.value.season[0].content[0].releaseDate,
            price: response.value.season[0].content[0].price,
            duration: response.value.season[0].content[0].duration,
            uuid: response.value.season[0].content[0].uuid,
            audioLang: response.value.season[0].content[0].audioLang,
            subtitle: response.value.season[0].content[0].subtitle,
          });
          setCurrentSerieForLanding(response.value);
        } else {
          const response = await getContentById(uuid);
          setCurrentContentForLanding({ ...response.value });
          setCarrouselForLanding(response.value.category.id, "community");
        }
      } catch (error) {
        history.push("/404");
      }
    };
    fetch();
    return () => {
      removeContentWeblinkURL();
      removeCurrentContentForLanding();
      removeCurrentSerieForLanding();
      removeCarrouselForLanding();
    };
  }, [props.match.params.id]);

  useEffect(() => {
    console.log(currentContentLanding);
    if (currentContentLanding?.subtitle.length) {
      let subtitlesArray = currentContentLanding.subtitle.map(async (sub) => {
        let response = await axios.get(sub.url);
        const trackBlob = new Blob([response.data], {
          type: "text/plain;charset=utf-8",
        });
        const trackUrl = URL.createObjectURL(trackBlob);
        return { kind: "subtitles", src: trackUrl, srcLang: sub.lang, default: sub.lang === "es" };
      });
      Promise.all(subtitlesArray).then((response) => {
        console.log(response);
        setSubtitles(response);
      });
    }
  }, [currentContentLanding]);

  console.log("subtitle", subtitles);

  return (
    <main className={styles.contentLanding}>
      <GoBack />
      {currentContentLanding || currentSerieLanding ? (
        <HeaderLanding
          video={currentContentLanding}
          fromLanding={true}
          isEpisode={isEpisode}
          currentSerieLanding={currentSerieLanding}
          currentSeason={currentSeason}
          setCurrentSeason={setCurrentSeason}
        />
      ) : (
        <div className={styles.loading}>
          <Loading />
        </div>
      )}

      {weblinkURL ? <PlayerLanding isEpisode={isEpisode} currentSeason={currentSeason} subtitlesArray={subtitles} /> : null}
      {currentCarruselLanding ? <Slider name='Similares' key={currentCarruselLanding.id} content={currentCarruselLanding.content} /> : null}

      <div className={styles.contentLandingSerie}>{currentSerieLanding ? <SerieSlider content={currentSerieLanding.season[currentSeason].content} /> : null}</div>

      <div className={`${styles.carouselBackground} ${styles.fromLanding}`}>
        <img src={mainThumbUrl} alt='test' />
        <figure className={styles.carouselBottomGradient}></figure>
      </div>
    </main>
  );
};

const mapStateToProps = (state) => {
  return {
    currentContentLanding: state.contentReducer.currentContentLanding,
    currentSerieLanding: state.contentReducer.currentSerieLanding,
    contentPurchased: state.userReducer.contentPurchased,
    weblinkURL: state.contentReducer.weblinkURL,
    currentCarruselLanding: state.contentReducer.currentCarruselLanding,
  };
};
export default connect(mapStateToProps, {
  setPlayerDuration,
  setPlayerEnd,
  removeCarrouselForLanding,
  setCarrouselForLanding,
  setPlayerProgress,
  getWebLink,
  removeContentWeblinkURL,
  getSerieById,
  getContentById,
  setCurrentContentForLanding,
  setCurrentSerieForLanding,
  removeCurrentContentForLanding,
  removeCurrentSerieForLanding,
})(ContentLanding);
