import React, { useState, useEffect } from "react";
import styles from "./HeaderLanding.module.scss";
import ContentInfo from "../UI/ContentInfo/ContentInfo";
import SeasonSwitch from "../UI/SwitchSeason/SwitchSeason";
import { FLIXX_DECIMALS, TYPE_SERIE, flixxos } from "../../utils/constants";
import { getTileCoverUrl, getSwitchingThumbUrl, getSwitchingThumbUrlSerie } from "../../utils/thumbsManager";
const HeaderLanding = ({ video, currentSerieLanding, fromLanding, isEpisode, currentSeason, setCurrentSeason }) => {
  let videoImage = currentSerieLanding && video.noEpisodeSelected ? currentSerieLanding : video;
  const mainThumbUrl = currentSerieLanding ? getSwitchingThumbUrlSerie(videoImage, "cover", 0, true) : getTileCoverUrl(videoImage);

  const [animateContent, SetAnimateContent] = useState(true);

  useEffect(() => {
    SetAnimateContent(true);
    setTimeout(() => {
      SetAnimateContent(false);
    }, 800);
  }, [video.body]);

  return (
    <div className={styles.headerLanding}>
      <div className={styles.headerLanding_caratulacontainer}>
        <div className={`${styles.headerLanding_caratula} ${animateContent ? styles.animation : ""}`}>
          <img src={mainThumbUrl} alt='' />
        </div>
        {currentSerieLanding ? <SeasonSwitch desktop season={currentSerieLanding.season} currentSeason={currentSeason} setCurrentSeason={setCurrentSeason} /> : null}
      </div>

      <ContentInfo desktop video={video} fromLanding={fromLanding} isEpisode={isEpisode} currentSerieLanding={currentSerieLanding} currentSeason={currentSeason} setCurrentSeason={setCurrentSeason} />
    </div>
  );
};

export default HeaderLanding;
