import React, { useState } from "react";
import styles from "./RegisterForm.module.scss";
import Button from "../../../UI/ButtonForm/ButtonForm";
import SelectCountry from "../../../UI/SelectCountry/SelectCountry";
import { useForm } from "react-hook-form";
import { COUNTRY_CODES } from "../../../../utils/countryCodes";
import { errorStringHandler } from "../../../../utils/ErrorStrings.js";
const Step2 = ({ mobile, setMobile, code, setCode, currentCountryCode, setCurrentCountryCode, options, registerHandler, errorFromAPI, isRegistering }) => {
  const { handleSubmit, register, errors, setError, watch, clearErrors, formState } = useForm({});

  const onSubmit = (values) => {
    console.log(values);
    registerHandler(values.phone);
  };

  let mobilePattern = /^\+?[1-9]\d{1,14}$/;

  console.log("errors", errors);

  let placeholder = currentCountryCode.value === "+54" ? "11 xxxx xxxx" : "Ingresa tu numero de telefono";
  return (
    <>
      <h2 className={styles.subtitle}>Flixxo es una billetera que maneja criptomonedas. Es fundamental por tu seguridad validar tu cuenta con tu numero de telfono.</h2>

      <form className={styles.form} onSubmit={handleSubmit(onSubmit)}>
        <SelectCountry options={options} currentCountryCode={currentCountryCode} setCurrentCountryCode={setCurrentCountryCode} />
        <div className={styles.registerForm__recoverInputContainer}>
          <input value={currentCountryCode.value} type='text' className={`${styles.registerForm__input} ${styles.registerForm__inputCountryCode}`} readOnly />
          <input
            type='text'
            placeholder={placeholder}
            name='phone'
            id='phone'
            ref={register({ required: "Required", pattern: mobilePattern, minLength: 8, maxLength: 15 })}
            className={`${styles.registerForm__input}  ${styles.registerForm__inputRecover}`}
          />
        </div>
        <span className={styles.error}>
          {errorFromAPI?.type === "mobileNumber" && errorStringHandler(errorFromAPI.message)}
          {(errors.phone?.type === "pattern" || errors.phone?.type === "minLength" || errors.phone?.type === "maxLength") && "Ingresa un numero valido."}
          {errors.phone?.type === "required" && "Este campo es requerido."}
        </span>

        <Button type={"submit"} customStyle={styles.btn} isLoading={isRegistering}>
          Crear cuenta
        </Button>
      </form>
    </>
  );
};

export default Step2;
