import React, { useState } from "react";
import styles from "./Switch.module.scss";
import { connect } from "react-redux";
import { reset as contentReset, changeCommunityFilterContent } from "../../../store/actions/content";
import { changeCommunityFilter } from "../../../store/actions/user";
import { COMMUNITY_FILTER } from "../../../utils/constants";
import { useHistory } from "react-router-dom";
const Switch = ({ communityFilter, changeCommunityFilterContent, changeCommunityFilter, isSearching }) => {
  const [activeFilter, setActiveFilter] = useState(communityFilter);
  const history = useHistory();

  const changeFilter = (param) => {
    if (history.location.pathname.includes("community") || history.location.pathname.includes("series") || history.location.pathname.includes("search")) {
      history.push("/");
    } else {
      if (param !== communityFilter) {
        setActiveFilter(param);
        changeCommunityFilter(param);
        changeCommunityFilterContent(param);
      }
    }
  };
  return (
    <div className={`${styles.switchContainer} ${isSearching ? styles.isSearching : ""}`}>
      <div className={`${styles.switchItem} ${activeFilter === COMMUNITY_FILTER.SERIES ? styles.active : ""}`} onClick={() => changeFilter(COMMUNITY_FILTER.SERIES)}>
        <h2>SERIES</h2>
      </div>
      <div className={`${styles.switchItem} ${activeFilter === COMMUNITY_FILTER.COMMUNITY ? styles.active : ""}`} onClick={() => changeFilter(COMMUNITY_FILTER.COMMUNITY)}>
        <h2>COMUNIDAD</h2>
      </div>
    </div>
  );
};

const mapStateToProps = (state) => {
  return {
    communityFilter: state.userReducer.communityFilter,
  };
};

export default connect(mapStateToProps, { changeCommunityFilterContent, changeCommunityFilter })(Switch);
