import React, { useState } from "react";
import { Slide, Image } from "pure-react-carousel";
import "pure-react-carousel/dist/react-carousel.es.css";
import styles from "./SliderItem.module.scss";
import { useHistory } from "react-router-dom";
import { getTileCoverUrl, getSwitchingThumbUrlSerie } from "../../../utils/thumbsManager";
import { TYPE_SERIE } from "../../../utils/constants";
import { connect } from "react-redux";
import { setCurrentContentForLanding } from "../../../store/actions/content";
import { showAlertModal } from "../../../store/actions/auth";
import Lottie from "react-lottie";
import hoverData from "../../../assets/flixxo-hover.json";
import "./draw.scss";

const defaultOptions = {
  loop: true,
  autoplay: false,
  rendererSettings: {
    preserveAspectRatio: "xMidYMid meet",
  },
};

const lottieStyle = {
  width: "100%",
  height: "100%",
  position: "absolute",
  top: "50%",
  left: "50%",
  transform: "translate(-50%, -50%)",
  zIndex: "10000",
  pointerEvents: "none",
};

const SliderItem = ({ video, index, setCurrentContentForLanding, showAlertModal, isLoggedIn, fromSearch }) => {
  const [isStopped, setIsStopped] = useState(true);

  const getMainThumbUrl = () => {
    const mainThumbUrl = video.contentType === TYPE_SERIE || video.isSerieEpisode === true ? getSwitchingThumbUrlSerie(video, "cover", 0, true) : getTileCoverUrl(video);

    return mainThumbUrl;
  };

  const mainThumbUrl = getMainThumbUrl();
  const history = useHistory();
  let communityFilter = video.contentType === 1 ? "community" : "series";

  const checkTranslate3d = () => {
    if (index === 0) {
      return styles.translateFirst;
    } else if (index === 5) {
      return styles.translateLast;
    }
  };
  const setContentHandler = () => {
    history.push(`/${communityFilter}/${video.uuid}`);
  };

  const hoverHandler = () => {
    if (!isStopped) {
      setIsStopped(true);
    } else {
      setIsStopped(false);
    }
  };

  const contentSlide = () => {
    return (
      <Slide index={index} className={`${styles.slide} ${checkTranslate3d()}`} style={{ animationDelay: `${index + 3}00ms` }} onClick={setContentHandler} classNameHidden={styles.hidden} classNameVisible={styles.visible} onMouseEnter={hoverHandler} onMouseLeave={hoverHandler}>
        <Lottie options={{ animationData: hoverData, ...defaultOptions }} style={lottieStyle} isClickToPauseDisabled={true} isStopped={isStopped} />
        <figure className={`${styles.gradient}`}></figure>
        <Image src={mainThumbUrl} alt={video.title} onClick={setContentHandler} />
      </Slide>
    );
  };

  const content = () => {
    return (
      <div className={styles.slideSearch} onMouseEnter={hoverHandler} onMouseLeave={hoverHandler} onClick={setContentHandler}>
        <Lottie options={{ animationData: hoverData, ...defaultOptions }} style={lottieStyle} isClickToPauseDisabled={true} isStopped={isStopped} />
        <figure className={`${styles.gradient}`}></figure>
        <img src={mainThumbUrl} alt={video.title} />
      </div>
    );
  };

  return fromSearch ? content() : contentSlide();
};

const mapStateToProps = (state) => {
  return {
    communityFilter: state.userReducer.communityFilter,
    isLoggedIn: state.authReducer.isLoggedIn,
  };
};

export default connect(mapStateToProps, { setCurrentContentForLanding, showAlertModal })(SliderItem);
