import React from "react";
import styles from "./SerieSlider.module.scss";
import { Slide, Image } from "pure-react-carousel";
import { FLIXX_DECIMALS, TYPE_SERIE, flixxos } from "../../../utils/constants";
import { getTileCoverUrl, getSwitchingThumbUrl, getSwitchingThumbUrlSerie } from "../../../utils/thumbsManager";
import { setCurrentContentForLanding } from "../../../store/actions/content";
import { connect } from "react-redux";
const SerieSliderItem = ({ index, video, setCurrentContentForLanding, currentContentLanding, contentPurchased }) => {
  const getMainThumbUrl = () => {
    const mainThumbUrl = video.contentType === TYPE_SERIE || video.isSerieEpisode === true ? getSwitchingThumbUrlSerie(video, "cover", 0, true) : getTileCoverUrl(video);

    return mainThumbUrl;
  };
  const mainThumbUrl = getMainThumbUrl();
  let episodeNumber = index + 1;

  if (episodeNumber < 10) {
    episodeNumber = `0${episodeNumber}`;
  }

  let minutes = Math.floor(video.duration / 60);
  const findInPurchased = (contentToFind) => {
    return contentPurchased.find((cont) => {
      return cont.uuid === contentToFind.uuid;
    });
  };

  const bodyLimited = video.body.slice(0, 150);
  const isPurchased = findInPurchased(video);
  const isSelected = currentContentLanding.uuid === video.uuid;
  return (
    <Slide
      index={index}
      style={{ animationDelay: `1${index < 5 ? index + 1 : 3}00ms` }}
      className={styles.slide}
      onClick={() => {
        window.scrollTo({
          top: 0,
        });
        setCurrentContentForLanding({ ...video, isPurchased: isPurchased, episodeNumber: episodeNumber });
      }}
    >
      <div className={styles.serieSlider}>
        <div
          className={`${styles.serieSliderThumbnail}`}
          // onClick={() => {
          //   window.scrollTo({
          //     top: 0,
          //   });
          //   setCurrentContentForLanding({ ...video, isPurchased: isPurchased, episodeNumber: episodeNumber });
          // }}
        >
          <Image src={mainThumbUrl} alt='' />
          <span className={styles.serieSliderEpisode}>{episodeNumber}</span>
          <figure className={` ${styles.gradient} ${isSelected ? styles.isSelected : ""}`}></figure>
        </div>
        <div className={styles.serieSliderInfo}>
          <h4>
            {video.title} | {minutes} min
          </h4>
          <p>{bodyLimited}.</p>
          <h3>{isPurchased ? " Disfrutala Ya!" : `Disponible por ${flixxos(video.price)} Flixx.`}</h3>
          {/* <button
            className={`${styles.serieSliderButton} ${!isPurchased ? styles.toPurchase : ""}`}
            onClick={() => setCurrentContentForLanding({ ...video, isPurchased: isPurchased, episodeNumber: episodeNumber })}
          >
            {isPurchased ? "REPRODUCIR" : "VER AHORA"}
          </button> */}
        </div>
      </div>
    </Slide>
  );
};

const mapStateToProps = (state) => {
  return {
    currentContentLanding: state.contentReducer.currentContentLanding,
    contentPurchased: state.userReducer.contentPurchased,
  };
};

export default connect(mapStateToProps, { setCurrentContentForLanding })(SerieSliderItem);
