import React, { useState } from "react";
import styles from "./CarouselWrapper.module.scss";
import { CarouselProvider, Slider, ButtonBack, ButtonNext, Dot } from "pure-react-carousel";
import "pure-react-carousel/dist/react-carousel.es.css";
import { ReactComponent as ArrowLeft } from "../../../assets/arrow-left.svg";
import { ReactComponent as ArrowRight } from "../../../assets/arrow-right.svg";
import { useMediaQuery } from "react-responsive";
const CarouselWrapper = ({ children, videos, length }) => {
  const isMobile = useMediaQuery({ query: "(max-width: 600px)" });
  const [showOptions, showOptionsHandler] = useState(false);
  return (
    <div className={styles.CarouselWrapper} onPointerEnter={() => showOptionsHandler(!showOptions)} onPointerLeave={() => showOptionsHandler(!showOptions)}>
      <CarouselProvider
        isIntrinsicHeight={true}
        isPlaying={false}
        interval={1000}
        naturalSlideWidth={1400}
        naturalSlideHeight={650}
        totalSlides={length}
        // dragEnabled={isMobile}
        // dragStep={isMobile}
        infinite={true}
        lockOnWindowScroll={true}
      >
        <Slider className={styles.slide} classNameAnimation={isMobile ? null : styles.animation} moveThreshold={0.1}>
          {children}{" "}
        </Slider>

        <div className={styles.CarouselWrapperDotContainer}>
          {[0, 1, 2, 3, 4].map((sli) => {
            let limit = length < 5 ? length : 5;
            if (sli < limit) {
              return <Dot className={styles.dot} slide={sli}></Dot>;
            }
          })}
        </div>

        <>
          <ButtonBack className={`${styles.button} ${styles.back}`}>
            <ArrowLeft />
          </ButtonBack>
          <ButtonNext className={`${styles.button} ${styles.next}`}>
            <ArrowRight />
          </ButtonNext>{" "}
        </>
      </CarouselProvider>
    </div>
  );
};

export default CarouselWrapper;
