import React from "react";
import Button from "../../../UI/ButtonForm/ButtonForm";
const PlayAsAnon = ({ styles, setTypeContent }) => {
  return (
    <>
      <h3 className={styles.modalAlert__contentSection__title}>Ya tens una cuenta?</h3>
      <Button onClick={() => setTypeContent("signIn")} customStyle={styles.playAsAnonMod}>
        Iniciar Sesin
      </Button>
      <Button onClick={() => setTypeContent("register")} customStyle={styles.playAsAnonMod}>
        Registrarme
      </Button>
    </>
  );
};

export default PlayAsAnon;
