export function errorStringHandler(string) {
  switch (string) {
    case "Email already registered":
      return "Email ya registrado";
    case "Nickname already in use":
      return "El nombre de usuario ya existe";
    case "Mobile number already registered":
      return "El nmero de telfono ya existe.";
    case "Incorrect Code":
      return "Cdigo incorrecto";
    case "Invalid credentials":
      return "Credenciales invlidas";
    case "Invalid code provided":
      return "Codigo invalido";
    default:
      break;
  }
}
