import React, { useEffect } from "react";
import { useForm } from "react-hook-form";
import styles from "./RegisterForm.module.scss";
import Button from "../../../UI/ButtonForm/ButtonForm";
import { errorStringHandler } from "../../../../utils/ErrorStrings.js";
const Step1 = ({ setStep, setFormData, formData, errorFromAPI }) => {
  const { handleSubmit, register, errors, setError, watch, clearErrors, formState } = useForm({
    defaultValues: { ...formData, password: "", confirmPassword: "" },
    mode: "onChange",
  });

  useEffect(() => {
    if (errorFromAPI) {
      setError(errorFromAPI.type, {
        type: "manual",
        message: errorFromAPI.message,
      });
    }
  }, [errorFromAPI]);

  const passPattern = /^(?=.*?[A-Z])(?=.*?[a-z])(?=.*?[0-9]).{8,}$/;

  const onSubmit = (values) => {
    console.log(values);
    setStep((s) => s + 1);
    setFormData(values);
  };

  console.log("errors", errors);

  return (
    <>
      <form className={styles.form} onSubmit={handleSubmit(onSubmit)}>
        <div className={`${styles.inputContainer} ${styles.nickname}`}>
          <input
            placeholder='Nombre de usuario'
            name='nickname'
            ref={register({ required: "Required" })}
            type='text'
            onChange={() => {
              if (errorFromAPI?.type === "nickname") {
                clearErrors("nickname");
              }
            }}
          />
        </div>
        <span className={styles.error}>
          {errors.nickname?.type === "required" && "Este campo es requerido"}
          {errors.nickname?.type === "manual" && errorStringHandler(errors.nickname?.message)}
        </span>
        <div className={`${styles.inputContainer} ${styles.email}`}>
          <input
            name='email'
            placeholder='Email'
            ref={register({ required: "Required" })}
            type='email'
            onChange={() => {
              if (errorFromAPI?.type === "email") {
                clearErrors("email");
              }
            }}
          />
        </div>
        <span className={styles.error}>
          {errors.email?.type === "required" && "Este campo es requerido"}
          {errors.email?.type === "manual" && errorStringHandler(errors.email?.message)}
        </span>
        <div className={`${styles.inputContainer} ${styles.password}`}>
          <input placeholder='Ingresa tu contrasea' name='password' ref={register({ required: "Required", pattern: passPattern })} type='password' />
        </div>
        <span className={styles.error}>
          {errors.password?.type === "required" && "Este campo es requerido"}
          {errors.password?.type === "pattern" && "Debe contener al menos una letra mayuscula, un numero y 8 caracteres."}
        </span>
        <div className={`${styles.inputContainer} ${styles.password}`}>
          <input
            placeholder='Confirma tu contrasea'
            name='confirmPassword'
            ref={register({ required: "Required", pattern: passPattern, validate: (value) => value === watch("password") })}
            type='password'
          />
        </div>
        <span className={styles.error}>
          {errors.confirmPassword?.type === "required" && "Este campo es requerido"}
          {errors.confirmPassword?.type === "pattern" && "Debe contener al menos una letra mayuscula, un numero y 8 caracteres."}
          {errors.confirmPassword?.type === "validate" && "La contrasea no coincide"}
        </span>
        <Button type={"submit"} customStyle={styles.btn} disabled={!formState.isValid}>
          Empez
        </Button>
      </form>
    </>
  );
};

export default Step1;
