import React, { useEffect, useState } from "react";
import styles from "./ContentInfo.module.scss";
import { connect } from "react-redux";
import ButtonPurchase from "../ButtonPurchase/ButtonPurchase";
import { ReactComponent as FlixxoCoin } from "../../../assets/flixxo-coin.svg";
import { ReactComponent as GoBack } from "../../../assets/arrow-left.svg";
import { Dot } from "pure-react-carousel";
import { GAHandler } from "../../../utils/GA";

import DurationAuthorInfo from "../DurationAuthorInfo/DurationAuthorInfo";

import { getWebLink, setContentWeblinkURL } from "../../../store/actions/content";
import { content as buyContent } from "../../../store/actions/payment";
import { getTileCoverUrl, getSwitchingThumbUrl, getSwitchingThumbUrlSerie, getDetailImageUrl, getHero } from "../../../utils/thumbsManager";
import { FLIXX_DECIMALS, TYPE_SERIE, flixxos, COMMUNITY_FILTER } from "../../../utils/constants";
import "pure-react-carousel/dist/react-carousel.es.css";
import { showAlertModal } from "../../../store/actions/auth";
import { get as getAd, toggleAdModal } from "../../../store/actions/ad";
import { setDismissNextEpisode, setPlayerEnd, resetNextEpisodeValues } from "../../../store/actions/media";
import { useHistory } from "react-router-dom";
import { setCurrentContentForLanding, removeContentWeblinkURL } from "../../../store/actions/content";
import { contentPurchased, balance as userBalance } from "../../../store/actions/user";
import { useMediaQuery } from "react-responsive";
import Button from "../../UI/Button/Button";
import SeasonSwitch from "../../UI/SwitchSeason/SwitchSeason";
import NextEpisode from "../NextEpisode/NextEpisode";

const ContentInfo = ({
  currentSeason,
  setCurrentSeason,
  video,
  toggleAdModal,
  index,
  setPlayerEnd,
  removeContentWeblinkURL,
  setCurrentContentForLanding,
  resetNextEpisodeValues,
  allCategories,
  setDismissNextEpisode,
  showAlertModal,
  isLoggedIn,
  currentSerieLanding,
  fromNextEpisode,
  contentPurchasedArray,
  fromLanding,
  fromHome,
  getWebLink,
  setContentWeblinkURL,
  balance,
  buyContent,
  getAd,
  communityFilter,
  contentPurchased,
  isEpisode,
  paymentContentPosting,
  userBalance,
  nextEpisodeNumber,
}) => {
  const history = useHistory();
  const isMobile = useMediaQuery({ query: "(max-width: 600px)" });
  const canPurchased = parseFloat(balance.amount) > parseFloat(video.price);

  const [animateContent, SetAnimateContent] = useState(true);

  useEffect(() => {
    if (fromLanding && !isMobile) {
      SetAnimateContent(true);
      setTimeout(() => {
        SetAnimateContent(false);
      }, 800);
    }
  }, [video.body]);

  const findInPurchased = (contentToFind) => {
    return contentPurchasedArray.find((cont) => {
      return cont.uuid === contentToFind.uuid;
    });
  };

  let isPurchased = findInPurchased(video);
  const contentType = video.contentType === 1 ? "Comunidad" : "Serie";
  const mainThumbUrl = getHero(video);
  const { title } = currentSerieLanding ? currentSerieLanding : video;
  const { body = "" } = "metadata" in video ? video.metadata[0] : video;
  const lang = video.audioLang || "";
  const category = allCategories.find((cat) => cat.id === video.categoryId);
  const relaseYear = video.releaseDate.split("-")[0];
  let minutes = Math.floor(video.duration / 60);

  const GABuyHandler = () => {
    if (communityFilter === COMMUNITY_FILTER.COMMUNITY) GAHandler("Comunidad", "Comunidad_CompraExitosa", video.price);
    else GAHandler("Series", "Series_CompraExitosa", video.price);
  };

  const GAPlayHandler = () => {
    if (communityFilter === COMMUNITY_FILTER.SERIES) {
      GAHandler("Series", "Series_ReproduccinExitosa", 0);
      GAHandler("Series", `Series_ReproduccinExitosa_${currentSerieLanding.category.name}`);
    } else {
      GAHandler("Comunidad", "Comunidad_ReproduccinExitosa", 0);
      GAHandler("Comunidad", `Comunidad_ReproduccinExitosa_${video.category.name}`);
    }
  };

  const buttonHandler = async () => {
    if (!isLoggedIn) {
      // setCurrentContentForLanding({ ...video, contentTypeFilter: communityFilter });
      GAHandler("Despliegue del Pop Up", "PopUp_ClickContenido");
      // showAlertModal("playAsAnon");
      if (fromHome) history.push(`/${contentType === "Comunidad" ? "community" : "series"}/${video.uuid}`);
      else showAlertModal("playAsAnon");
    } else {
      if (fromHome) {
        // history.push(`/community/${video.uuid}`);
        history.push(`/${contentType === "Comunidad" ? "community" : "series"}/${video.uuid}`);
      } else {
        if (isPurchased) {
          await getWebLink(video.uuid).then((weblink) => {
            if (fromNextEpisode) {
              setCurrentContentForLanding({
                ...video,
                isPurchased: isPurchased,
              });
              setPlayerEnd();
              removeContentWeblinkURL();
              resetNextEpisodeValues();
            }
            GAPlayHandler();
            GAHandler("Contenido", "Contenido_ReproduccinExitosa");
            setContentWeblinkURL(weblink.value);
          });
        } else if (canPurchased) {
          await buyContent({
            contentId: video.uuid,
            price: parseFloat(video.price),
          }).then(async () => {
            GAHandler("Contenido", "Contenido_CompraExitosa", video.price);
            GABuyHandler();
            contentPurchased(communityFilter);
            userBalance();
            //play
            await getWebLink(video.uuid).then((weblink) => {
              if (fromNextEpisode) {
                setCurrentContentForLanding({
                  ...video,
                  isPurchased: isPurchased,
                });
                setPlayerEnd();
                removeContentWeblinkURL();
                resetNextEpisodeValues();
              }
              GAPlayHandler();
              GAHandler("Contenido", "Contenido_ReproduccinExitosa");
              setContentWeblinkURL(weblink.value);
            });
          });
        } else if (!canPurchased) {
          // getAd();
          toggleAdModal();
        }
      }
    }
  };

  const omitHandler = () => {
    setDismissNextEpisode();
  };

  const author = isEpisode && !video.noEpisodeSelected ? currentSerieLanding.author : video.author;

  const bodyLimited = body.slice(0, fromNextEpisode ? 120 : 240);

  const buttonContentHandler = () => {
    if (canPurchased && !isPurchased && isLoggedIn) {
      return "VER AHORA";
    } else if (!canPurchased && !isPurchased && isLoggedIn && fromLanding) {
      return "MIR EL ANUNCIO Y GAN FLIXX";
    } else if (isPurchased) {
      return "REPRODUCIR";
    } else if (!isLoggedIn) {
      return "VER AHORA";
    } else {
      return "VER AHORA";
    }
  };

  //video.noEpisodeSelected

  const contentStatusHandler = () => {
    if (!canPurchased && isLoggedIn && video.price && !isPurchased) {
      return "No tienes suficientes crditos!";
    } else if (isPurchased) {
      return "Disfrutala Ya!";
    } else {
      return "Disponible por - ";
    }
  };

  let { episodeNumber } = video;

  console.log("fromLanding", fromLanding, "video", video);

  return (
    <div className={`${styles.contentInfoContainer} ${animateContent ? styles.animation : ""}`}>
      {isMobile && !fromLanding ? null : (
        <div className={`${styles.contentInfo_info} ${fromLanding ? styles.fromLanding : ""}  ${fromNextEpisode ? styles.fromNextEpisode : ""}`}>
          <h2 className={`${title.length >= 18 ? styles.minTitle : ""}`}>{title}</h2>
          {isEpisode && !video.noEpisodeSelected ? (
            <h3>{`Episodio ${fromNextEpisode ? nextEpisodeNumber : episodeNumber} \xa0 |   \xa0${video.title}   \xa0|  \xa0 ${minutes} min`}</h3>
          ) : (
            <h3>{`${relaseYear} \xa0 | \xa0 ${contentType} \xa0 |  \xa0 ${fromLanding ? video.category.name : video.category?.name || category.name}  \xa0 |  \xa0 Audio (${lang})`}</h3>
          )}

          {fromNextEpisode || isMobile || !fromLanding ? null : <DurationAuthorInfo author={author} minutes={minutes} showDuration={video.noEpisodeSelected} />}

          {isMobile ? null : (
            <p className={styles.description} style={{ marginBottom: !video.price ? "20px" : "" }}>
              {bodyLimited}
            </p>
          )}
        </div>
      )}
      {video.price && !isMobile ? (
        <div className={`${styles.contentInfo_videoStatus} ${fromLanding ? styles.fromLanding : ""} ${fromNextEpisode ? styles.fromNextEpisode : ""}`}>
          <span className={` ${!canPurchased && isLoggedIn && video.price && !isPurchased ? styles.cantPurchase : ""}`}>{contentStatusHandler()} </span>
          {isPurchased ? null : (
            <>
              <FlixxoCoin className={styles.contentInfo_videoStatus_coin} />
              <span>{video.price ? flixxos(video.price) : ""} Flixx</span>{" "}
            </>
          )}
        </div>
      ) : null}

      {/* {!canPurchased && isLoggedIn && video.price ? <p className={`${styles.cantPurchase} ${fromLanding ? styles.fromLanding : ""}`}>No tienes suficientes creditos!</p> : null} */}

      <div className={`${styles.buttonContainer} ${fromNextEpisode ? styles.fromNextEpisode : ""}`}>
        <Button
          onClick={buttonHandler}
          fromLanding={fromLanding}
          changeStatus={(isPurchased || !canPurchased) && isLoggedIn && video.price}
          animation={styles.buttonAnimation}
          isLoading={paymentContentPosting}
          fromNextEpisode={fromNextEpisode}
          isLoggedIn={isLoggedIn}
          canPurchased={canPurchased}
        >
          {buttonContentHandler()}
        </Button>

        {fromNextEpisode ? (
          <Button
            onClick={omitHandler}
            omitir={true}
            fromLanding={fromLanding}
            changeStatus={(isPurchased || !canPurchased) && isLoggedIn}
            animation={styles.buttonAnimation}
            isLoading={paymentContentPosting}
            fromNextEpisode={fromNextEpisode}
          >
            OMITIR
          </Button>
        ) : null}
      </div>
      {isMobile && fromLanding && !fromNextEpisode ? <DurationAuthorInfo author={author} minutes={minutes} showDuration={video.noEpisodeSelected} /> : null}
      {isMobile && fromLanding && !fromNextEpisode ? <p className={styles.description}>{bodyLimited}</p> : null}
      {currentSerieLanding ? <SeasonSwitch season={currentSerieLanding.season} currentSeason={currentSeason} setCurrentSeason={setCurrentSeason} /> : null}

      {/* <ButtonPurchase /> */}
    </div>
  );
};

const mapStateToProps = (state) => {
  return {
    allCategories: state.categorieReducer.categories,
    isLoggedIn: state.authReducer.isLoggedIn,
    balance: state.userReducer.balance,
    communityFilter: state.userReducer.communityFilter,
    currentSerieLanding: state.contentReducer.currentSerieLanding,
    contentPurchasedArray: state.userReducer.contentPurchased,
    paymentContentPosting: state.paymentReducer.paymentContentPosting,
  };
};

export default connect(mapStateToProps, {
  toggleAdModal,
  showAlertModal,
  setPlayerEnd,
  removeContentWeblinkURL,
  resetNextEpisodeValues,
  getWebLink,
  setDismissNextEpisode,
  setContentWeblinkURL,
  buyContent,
  getAd,
  setCurrentContentForLanding,
  contentPurchased,
  userBalance,
})(ContentInfo);
