import React, { useRef, useEffect, useState } from "react";
import styles from "./PlayerLanding.module.scss";
import { connect } from "react-redux";
import VideoPlayer from "../VideoPlayer/VideoPlayer";
import NextEpisode from "../UI/NextEpisode/NextEpisode";
import { ReactComponent as Close } from "../../assets/close2.svg";
import { setPlayerDuration, setPlayerEnd, setPlayerProgress, setPlayerStart, setBufferStart, setBufferEnd, resetNextEpisodeValues } from "../../store/actions/media";
import { removeContentWeblinkURL } from "../../store/actions/content";
import CastButton from "../UI/CastButton/CastButton";
const PlayerLanding = ({
  isEpisode,
  weblinkURL,
  bufferEnd,
  setPlayerEnd,
  currentSeason,
  setBufferStart,
  setBufferEnd,
  resetNextEpisodeValues,
  currentSerieLanding,
  setPlayerStart,
  currentContentLanding,
  setPlayerProgress,
  setPlayerDuration,
  removeContentWeblinkURL,
  subtitlesArray,
}) => {
  const endHandler = () => {
    setPlayerEnd();
    removeContentWeblinkURL();
    resetNextEpisodeValues();
  };

  //   label: null
  // lang: "en"
  // url: "https://s3.amazonaws.com/pro-flixxo-assets/content-subtitle/1562009539137"
  // uuid: "46d8cd27-18f5-48e8-b03b-a392d9a22c43"

  // useEffect(() => {
  //   if (weblinkURL) {
  //     setPlayerStart();
  //   } else if (!weblinkURL) {
  //     setPlayerEnd();
  //   }

  //   //onReady={() => setPlayerStart()}
  // }, [weblinkURL]);

  return (
    <div className={styles.videoContainer}>
      <Close className={styles.videoContainerClose} onClick={endHandler} />
      <CastButton weblinkURL={weblinkURL} />
      {weblinkURL ? (
        <VideoPlayer
          onBuffer={() => {
            setBufferStart();
          }}
          onReady={() => {
            setBufferEnd();
          }}
          url={weblinkURL}
          playing={true}
          onDuration={(dur) => setPlayerDuration(dur)}
          onProgress={(progr) => setPlayerProgress(progr)}
          onEnded={endHandler}
          width={"100%"}
          height={"100%"}
          subtitles={subtitlesArray}
        />
      ) : null}
      {isEpisode ? <NextEpisode currentSeason={currentSeason} currentSerieLanding={currentSerieLanding} currentContentLanding={currentContentLanding} /> : null}
    </div>
  );
};

const mapStateToProps = (state) => {
  return {
    currentContentLanding: state.contentReducer.currentContentLanding,
    currentSerieLanding: state.contentReducer.currentSerieLanding,
    weblinkURL: state.contentReducer.weblinkURL,
    hasEnded: state.mediaReducer.playerHasEnd,
    bufferEnd: state.mediaReducer.bufferEnd,
  };
};

export default connect(mapStateToProps, { setPlayerProgress, setPlayerEnd, setBufferStart, setBufferEnd, resetNextEpisodeValues, setPlayerDuration, removeContentWeblinkURL, setPlayerStart })(
  PlayerLanding
);
