import React, { useEffect, useState, useRef } from "react";
import styles from "./Nav.module.scss";
import { ReactComponent as BurgerMenu } from "../../assets/burger-nav.svg";
import { ReactComponent as FlixxoNavLogo } from "../../assets/flixxo-logo-nav.svg";
import { ReactComponent as FlixxoCoin } from "../../assets/flixxo-coin.svg";
import { FLIXX_DECIMALS, flixxos } from "../../utils/constants";
import DefaultProfilePic from "../../assets/ProfilePic.svg";
import Search from "./Search/Search";
import Switch from "./Switch/Switch";
import { connect } from "react-redux";
import { showAlertModal } from "../../store/actions/auth";
import { get as getAd, toggleAdModal } from "../../store/actions/ad";
import _ from "lodash";
import { Link, useHistory } from "react-router-dom";
import SideNav from "../UI/SideNav/SideNav";
import Lottie from "react-lottie";
import loadingData from "../../assets/flixxo-loading-air.json";
import logoData from "../../assets/flixxo-logo.json";
import burgerData from "../../assets/burger-lottie.json";
import Coins from "../../assets/flixxo-coins.json";
import { useMediaQuery } from "react-responsive";
import LottieWeb from "lottie-web";
import CountUp from "react-countup";
import { ReactComponent as NavigateBack } from "../../assets/navigate-back.svg";
import { GAHandler } from "../../utils/GA";
import { motion, useAnimation, transform } from "framer-motion";

const defaultOptions = {
  loop: false,
  autoplay: true,
};

const coinOptions = {
  loop: false,
  autoplay: false,
};

const lottieStyle = {
  position: "absolute",
  top: "-10px",
  right: "-10px",
};

const UserInfo = ({ avatar, _parseBalance, isSearching }) => {
  const controls = useAnimation();
  let { amount, prevAmount } = _parseBalance();
  const [isStopped, setIsStopped] = useState(true);
  const isInitialMount = useRef(true);

  let eventListenerCoin = [
    {
      eventName: "complete",
      callback: () => {
        setIsStopped(true);
      },
    },
  ];

  useEffect(() => {
    if (isInitialMount.current) {
      isInitialMount.current = false;
    } else {
      controls.start({
        scale: 1,
        transition: {
          type: "spring",
          velocity: 50,
          stiffness: 700,
          damping: 80,
        },
      });
      setIsStopped(false);
    }
  }, [amount]);

  return (
    <>
      <div className={`${styles.nav__user__coins}`}>
        {/* {showLottie ? <Lottie options={{ animationData: loadingData, ...defaultOptions }} width={300} speed={1.5} style={lottieStyle} eventListeners={eventListeners} /> : <FlixxoCoin />} */}
        <FlixxoCoin className={styles.nav__user__svg} />
        <motion.span animate={controls} style={{ color: "#FFFFFF" }}>
          <CountUp start={prevAmount} end={amount} duration={2} decimals={2} decimal='.'></CountUp>
        </motion.span>
        <Lottie options={{ animationData: Coins, ...coinOptions }} width={300} speed={1.5} style={lottieStyle} isClickToPauseDisabled={true} isStopped={isStopped} eventListeners={eventListenerCoin} />
      </div>
      <div className={styles.nav__user__image}>
        <img src={avatar} alt='persona ' />
      </div>
    </>
  );
};

const Nav = ({ showAlertModal, isLoggedIn, user, balance, adReward, getAd, adGot, toggleAdModal, isSearching }) => {
  const lottieLogoRef = useRef(null);
  const [lottieLogoAnimation, setLottieLogoAnimation] = useState(null);
  const history = useHistory();

  const lottieBurgerRef = useRef(null);
  const [burgerAnimation, setburgerAnimation] = useState(null);
  // const [isPaused, setIsPaused] = useState(false)
  // const [goToValue, setGoToValue] = useState(null)

  useEffect(() => {
    if (lottieLogoRef && !lottieLogoAnimation) {
      let lottieAnimation = LottieWeb.loadAnimation({
        container: lottieLogoRef.current,
        renderer: "svg",
        loop: false,
        autoplay: true,
        animationData: logoData,
        rendererSettings: {
          preserveAspectRatio: "xMidYMid slice",
          viewBoxOnly: true,
          className: styles.nav__actions_logo_lottie,
        },
      });
      setLottieLogoAnimation(lottieAnimation);
    }

    if (lottieBurgerRef && !burgerAnimation) {
      let lottieBurger = LottieWeb.loadAnimation({
        container: lottieBurgerRef.current,
        renderer: "svg",
        loop: false,
        autoplay: false,
        animationData: burgerData,
        rendererSettings: {
          preserveAspectRatio: "xMidYMid slice",
          viewBoxOnly: true,
          className: styles.burger,
        },
      });
      lottieBurger.setSpeed(2);
      setburgerAnimation(lottieBurger);
    }
  }, [lottieLogoRef, lottieLogoAnimation]);

  const isMobile = useMediaQuery({ query: "(max-width: 600px)" });

  const isTab = useMediaQuery({ query: "(max-width: 1100px)" });
  const [scroll, setScroll] = useState(false);
  const [showSideNav, showSideNavHandler] = useState(false);

  useEffect(() => {
    document.addEventListener("scroll", () => {
      const scrollCheck = window.scrollY > 10;
      if (scrollCheck !== scroll) {
        setScroll(scrollCheck);
      } else {
        setScroll(false);
      }
    });
  }, []);

  const _parseBalance = () => {
    let amount = 0;
    let prevAmount = 0;
    if (!_.isEmpty(balance)) {
      amount = flixxos(balance.amount);
      prevAmount = flixxos(balance.prevAmount);
    }
    return { amount, prevAmount };
  };
  const _parseAdReward = () => {
    if (adReward) {
      return flixxos(adReward);
    }
    return 0;
  };

  const rewardsHandler = () => {
    // if (!isLoggedIn) {
    //   toggleAdModal();
    //   GAHandler("Despliegue del Pop Up", "PopUp_ClickContenido");
    // } else {
    //   // getAd();
    //   GAHandler("Despliegue del Pop Up", "PopUp_ClickContenido");
    //   toggleAdModal();
    // }
    GAHandler("Despliegue del Pop Up", "PopUp_ClickContenido");
    toggleAdModal();
  };

  const isLoggedInContent = () => {
    if (isLoggedIn && !isMobile) {
      let avatar = DefaultProfilePic;
      if (user.profile) avatar = user.profile.avatar ? user.profile.avatar : DefaultProfilePic;
      return <UserInfo _parseBalance={_parseBalance} avatar={avatar} isSearching={isSearching} />;
    } else if (!isLoggedIn && !isMobile) {
      return (
        <button
          className={`${styles.navButton} ${styles.navButton__signIn} ${isSearching ? styles.isSearching : ""}`}
          onClick={() => {
            GAHandler("Despliegue del Pop Up", "PopUp_ClickLogin");
            showAlertModal("playAsAnon");
          }}
        >
          INICIAR SESIN
        </button>
      );
    } else {
      return null;
    }
  };

  const burgerMenuHandler = () => {
    if (showSideNav) burgerAnimation.playSegments([40, 0], true);
    else burgerAnimation.playSegments([0, 40], true);
    showSideNavHandler(!showSideNav);
  };

  const checkLocationMobile = isMobile && !(window.location.pathname.includes("series") || window.location.pathname.includes("community"));
  const checkLocationMobile2 = isMobile && (window.location.pathname.includes("series") || window.location.pathname.includes("community"));

  return (
    <>
      {!isMobile ? <div ref={lottieBurgerRef} onClick={burgerMenuHandler} className={`${isSearching ? styles.isSearching : ""}`} /> : null}
      <nav className={`${styles.nav} ${scroll ? styles.scrolled : ""}`}>
        <div className={styles.navContainer}>
          {/* {checkLocationMobile ? <Search /> : null} */}
          {checkLocationMobile2 ? <NavigateBack onClick={() => history.push("/")} style={{ width: "18px", height: "18px", marginRight: "20px", display: "flex", flex: "0 1 auto" }} /> : null}
          <div className={`${styles.nav__actions} ${isMobile ? styles.isMobile : ""}`}>
            {!isMobile ? (
              <>
                <Link
                  to='/'
                  className={`${styles.nav__actions_logo} ${isSearching ? styles.isSearching : ""}`}
                  onMouseEnter={() => {
                    // lottieLogoAnimation.triggerEvent("complete");
                    if (lottieLogoAnimation.currentFrame >= lottieLogoAnimation.totalFrames - 20) {
                      lottieLogoAnimation.playSegments([0, 90], true);
                    }
                  }}
                >
                  <div ref={lottieLogoRef} className={`${isSearching ? styles.isSearching : ""}`}></div>
                </Link>
                <Switch isSearching={isSearching} />
                <Search />
              </>
            ) : (
              <>
                <Link to='/' className={styles.nav__actions_logo}>
                  <FlixxoNavLogo style={{ width: "86px" }} />
                </Link>
                <Search isMobile />
              </>
            )}
          </div>
          {isMobile || isTab ? null : (
            <div className={styles.nav__winFlixx}>
              <button className={`${styles.navButton} ${isSearching ? styles.isSearching : ""}`} onClick={rewardsHandler}>
                {/* GAN {isLoggedIn && !isMobile ? _parseAdReward() : ""} FLIXX */}
                GAN FLIXX
              </button>
            </div>
          )}
          <div className={styles.nav__user}>{isLoggedInContent()}</div>
        </div>
        {isMobile && !(window.location.pathname.includes("series") || window.location.pathname.includes("community") || window.location.pathname.includes("search")) ? <Switch /> : null}
      </nav>
      <SideNav showSideNavHandler={showSideNavHandler} showSideNav={showSideNav} showAlertModal={showAlertModal} isLoggedIn={isLoggedIn} />
    </>
  );
};

const mapStateToProps = (state) => {
  return {
    user: state.userReducer.user,
    balance: state.userReducer.balance,
    adReward: state.adReducer.adReward,
    adGot: state.adReducer.adGot,
    isSearching: state.searchReducer.isSearching,
  };
};

export default connect(mapStateToProps, { showAlertModal, getAd, toggleAdModal })(Nav);
